@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
* {
  box-sizing: border-box;
}


body,
#root {
  margin: 0;
  background-color: #d4cccb;
  font-family: Arial, Montserat, sans-serif;
  width: 100%;
  height: 100vh;
}

.app-container {
  max-width: 50%;
  margin: 0 auto;
  position: relative;
  top: 300px;
  background: #fff;
  padding: 20px;
  margin-bottom: 400px;
}

li {
  list-style-type: none;
  cursor: pointer;
}

form {
  position: relative;
  width: 80%;
  margin: 0 auto;
  margin-top: 40px;
  text-align: center;
  align-items: center;
  display: flex;
  flex-direction: column;
}

form label {
  margin: 0 auto;
  margin-bottom: 40px;
  position: relative;
  padding-bottom: 10px;
  border-bottom: dashed 1px #ddd;
}

form input {
  margin: 0 auto;
  margin-top: 40px;
  position: relative;
}

h1 {
  font-size: 1.8em;
}
p {
  text-align: center;
  font-weight: 500;
}
input[type="text"],
select {
  width: 100%;
  padding: 12px;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
  margin-top: 6px;
  margin-bottom: 16px;
  resize: vertical;
}
input[type="submit"] {
  background-color: #4caf50;
  color: white;
  padding: 12px 20px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  position: relative;
  margin-top: 40px;
}
input[type="submit"]:hover {
  background-color: #45a049;
}
.container {
  border-radius: 5px;
  background-color: #f2f2f2;
  padding: 20px;
}

/* modal*/

.react-reveal {
  position: relative;
  margin-bottom: 15px;
  width: 100%;
}

.modal-body {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  max-height: 100%;
  min-width: 100%;
  width: 100%;
  background-color: #d4cccb;
}

.close-modal {
  position: absolute;
  right: 0em;
  top: 3.9em;
  cursor: pointer;
  background: #bd2525;
  border: none;
  color: #fff;
  z-index: 1000;
  width: 40px;
  height: 40px;
  border-radius: 50%;
}

.close-modal:hover {
  background: #e46262;
}

.MuiCardContent-root {
  padding: 16px;
  position: relative;
  min-height: 300px;
  text-align: center;
}


.text-danger h2 {
  position: relative;
  top: 25px;
  line-height: 1.8;
  color: #000;
  font-size: 1.2em;
  font-weight: bold;
  padding-top: 10px;
  border-top: dashed 1px #ddd;
}

.flex-center {
  text-align: center;
    position: relative;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-self: center;
    align-items: center;
    margin: 0 auto;
}

@media screen and (min-width: 300px) and (max-width: 550px) {
  .app-container {
    max-width: 75%;
    margin: 0 auto;
    position: relative;
    top: 300px;
    background: #fff;
    padding: 20px;
    margin-bottom: 400px;
  }
}

:root {
  --ease: all 0.5s ease-out;
  --primary-color: #444;
  --secondary-color: #55abf1;
}

.header {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  align-items: center;
  height: 6em;
  position: relative;
  background: #444;
  background: var(--primary-color);
}

.header img {
  width: 80px;
  height: 80px;
  border-radius: 50%;
  position: relative;
  top: -16px;
  left: -55px;
}

.link {
  font-size: 1em;
  border: 3px #fff solid;
  padding: 1em 2.5em;
  margin-bottom: 3rem;
  transition: all 0.5s ease-out;
  transition: var(--ease);
  position: relative;
  top: 4px;
}

.link:hover {
  padding: 0.8rem 1.9rem;
  background: #55abf1;
  background: var(--secondary-color);
  color: #55abf1;
  color: var(--secondary-color);
}

.footer-bottom {
  background: #333;
  color: white;
  padding: 10px;
  bottom: 0px;
  left: 0;
  width: 100%;
  text-align: center;
  z-index: 120;
  position: relative;
  height: 5em;
}

.footer-bottom .text-center {
  position: relative;
  top: 5px;
}

.photo-container {
    position: absolute;
    width: 10vw;
    min-width: 100%;
    height: 300px;
    opacity: 1;
    top: 62px;
    left: 0px;
    margin-bottom: 50px;
    background: url('https://source.unsplash.com/random') no-repeat center center fixed;
    background-size: cover;
}
.App {
  text-align: center;
}



